import React, { Component } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookF, faTwitter, faGithub, faLinkedin, faReddit } from "@fortawesome/free-brands-svg-icons"

// import { Link, StaticRouter } from "react-router-dom";

class NavRight extends Component {
    render() {
        return (
            <div className="site-nav-right">
                <div className="social-links">
                    <a
                        className="social-link social-link-fb"
                        href="https://www.facebook.com/hmthanhgm"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <FontAwesomeIcon icon={faFacebookF} size="lg" />
                    </a>

                    <a
                        className="social-link social-link-tw"
                        href="https://twitter.com/hmthanhgm"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <FontAwesomeIcon icon={faTwitter} size="lg" />
                    </a>

                    <a
                        className="social-link social-link-fb"
                        href="https://www.github.com/hmthanh"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <FontAwesomeIcon icon={faGithub} size="lg" />
                    </a>

                    <a
                        className="social-link social-link-tw"
                        href="https://www.linkedin.com/in/hmthanh"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <FontAwesomeIcon icon={faLinkedin} size="lg" />
                    </a>
                    <a
                        className="social-link social-link-tw"
                        href="https://www.reddit.com/user/hmthanh"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <FontAwesomeIcon icon={faReddit} size="lg" />
                    </a>
                </div>
            </div>
        );
    }
}

export default NavRight;