import React, { Component } from 'react';
import HeaderTag from '../../components/Header/HeaderTag';
import Footer from '../../components/Footer/Footer';
import PostCard from '../../components/PostCard/PostCard'
import * as Const from '../../constants/const';
import * as Data from '../../constants/data';


class Tags extends Component {
    componentDidMount() {
        document.body.className = Const.BODY_TAG;
    }
    render() {
        var { match } = this.props;
        var tag = match.params.name;
        var tagName = Data.TAGS[tag];

        var posts = Data.findDataByTag(tag);
        var element = posts.map((value, index) => {
            return <PostCard key={index} data={value}></PostCard>
        });

        var numPost = posts.length;

        return (
            <div>
                <HeaderTag tag={tagName} numPost={numPost}></HeaderTag>
                <main id="site-main" className="site-main outer">
                    <div className="inner">
                        <div className="post-feed">
                            {element}
                        </div>
                    </div>
                </main>
                <Footer></Footer>
            </div>
        );
    }
}

export default Tags;