import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';
import Aside from '../../components/Aside/Aside';
import Progress from '../../components/MainView/Progress';
// import Subscribe from '../../components/MainView/Subscribe';
import AuthorCard from '../../components/AuthorCard/AuthorCard';
import Section1 from '../../components/Section/Section1';
import HeaderPost from '../../components/Header/HeaderPost';
import Footer from '../../components/Footer/Footer'
import * as Const from '../../constants/const';
import * as Data from '../../constants/data';


class Post extends Component {
    componentDidMount() {
        document.body.className = Const.BODY_POST;
    }

    render() {
        window.scrollTo(0, 0);
        var {match} = this.props;
        var url_title = match.params.title;
        var article = Data.findDataByTitle(url_title);
        var tag = article.tag;
        var tagName = Data.TAGS[tag];

        return (
            <div>
                <HeaderPost></HeaderPost>
                <main id="site-main" className="site-main outer">
                    <div className="inner">
                        <article className="post-full post tag-getting-started ">
                            <header className="post-full-header">
                                <section className="post-full-meta">
                                    <time className="post-full-meta-date" dateTime={article.time}>{article.time}</time>
                                    <span className="date-divider">/</span>
                                    <NavLink to={"/tag/" + tag}>{tag}</NavLink>
                                </section>
                                <h1 className="post-full-title">{article.title}</h1>
                            </header>
                            <figure className="post-full-image"
                                style={{ backgroundImage: "url("+article.img+")" }}>
                            </figure>
                            <Section1></Section1>
                            <AuthorCard></AuthorCard>
                        </article>
                    </div>
                </main>
                <Aside tagName={tagName} tag={tag}></Aside>
                <Progress data={article}></Progress>
                <Footer></Footer>
            </div>
        );
    }
}

export default Post;