/* eslint-disable jsx-a11y/accessible-emoji */
import React, { Component } from 'react';
import './AuthorCard.css';

class AuthorCard extends Component {
    render() {
        return (
            <footer className="post-full-footer">
                <section className="author-card">
                    <img className="author-profile-image" src="/img/awatar.jpg" alt="Hoang Minh Thanh" />
                    <section className="author-card-content">
                        <h4 className="author-card-name"><a href="../author/ghost/index.html">Minh-Thanh Hoang</a></h4>
                        <p>Sleep for dreaming, live for working</p>
                    </section>
                </section>
                <div className="post-full-footer-right">
                    <a className="author-card-button" href="../author/ghost/index.html">Read More</a>
                </div>
            </footer>
        );
    }
}

export default AuthorCard;