class HeaderHome extends React.Component {
    render() {
        //var background = {"img": "background-image: url(img/top.jpg)"};
        return (
            <header className="site-header outer" style={{ backgroundImage: "url(img/top.jpg)" }}>
                <div className="inner">
                    <div className="site-header-content">
                        <h1 className="site-title"> {Names.BLOG_NAME} </h1>
                        <h2 className="site-description"> {Names.SLOGAN}</h2>
                    </div>

                    <MenuDefault></MenuDefault>
                </div>
            </header>
        );
    }
}

export default HeaderHome;