import React, { Component } from "react";
import './PostCard.css';
import { NavLink } from 'react-router-dom';
import * as Const from '../../constants/const';

class PostCard extends Component {
    render() {
        var info = this.props.data;
        var mainUrl = info.url
        var imgUrl = info.img;
        return (
            <article className="post-card post tag-getting-started">
                <NavLink className="post-card-image-link" to={`/page/${mainUrl}`}>
                    <div className="post-card-image"
                        style={{ backgroundImage: "url(" + imgUrl + ")" }}>
                    </div>
                </NavLink>
                <div className="post-card-content">
                    <NavLink className="post-card-content-link" to={`/page/${mainUrl}`}>
                        <header className="post-card-header">
                            <span className="post-card-tags">{info.tag}</span>
                            <h2 className="post-card-title">{info.title}</h2>
                        </header>
                        <section className="post-card-excerpt">
                            <p>{info.desc}</p>
                        </section>
                    </NavLink>
                    <footer className="post-card-meta">
                        <ul className="author-list">
                            <li className="author-list-item">
                                <div className="author-name-tooltip">
                                    {Const.USER_NAME}
                                </div>
                                <a href="http://hmthanh.github.io" className="static-avatar author-profile-image">
                                    <img className="author-profile-image" src={Const.USER_AWATAR} alt={Const.USER_NAME} />
                            </a>
                        </li>
                    </ul>
                            <span className="reading-time">{info.time}</span>
                </footer>
            </div>
            </article >
                );
            }
        }
        
export default PostCard;