import React, { Component } from "react";

class Section1 extends Component {
  render() {
    return (
      <section className="post-full-content">
        <div className="post-content">
          <p>Hiện nay Python đang là ngôn ngữ phổ biến, tuy cú pháp ngắn gọn đơn giản nhưng rất mạnh mẽ.<br/>
          Đối với nhiều bạn mới làm quen với lập trình còn chưa tổng hợp tài liệu học Python đầy đủ, cũng như trực quan có thể thực hành ngay. Nên mình tổng hợp lại để những người mới làm quen với Python có thể dễ dàng bắt đầu từ cơ bản đến nâng cao</p>
          <h1 id="basic-tagging">Mô tả thuật toán</h1>
          <p>
            You can think of tags like Gmail labels. By tagging posts with one
            or more keyword, you can organise articles into buckets of related
            content.
          </p>
          <p>
            When you create content for your publication you can assign tags to
            help differentiate between categories of content.{" "}
          </p>
          <p>
            For example you may tag some content with  News and other content
            with Podcast, which would create two distinct categories of content
            listed on <code>/tag/news/</code> and
            <code>/tag/weather/</code>, respectively.
          </p>
          <p>
            If you tag a post with both <code>News</code> <em>and</em>{" "}
            <code>Weather</code> - then it appears in both sections. Tag
            archives are like dedicated home-pages for each category of content
            that you have. They have their own pages, their own RSS feeds, and
            can support their own cover images and meta data.
          </p>
          <h1 id="the-primary-tag">Mã giả</h1>
          <p>
            Inside the Ghost editor, you can drag and drop tags into a specific
            order. The first tag in the list is always given the most
            importance, and some themes will only display the primary tag (the
            first tag in the list) by default.{" "}
          </p>
          <blockquote>
            <em>
              <strong>News</strong>, Technology, Startup
            </em>
          </blockquote>
          <p>
            So you can add the most important tag which you want to show up in
            your theme, but also add related tags which are less important.
          </p>
          <h1 id="private-tags">Độ phức tạp thuật toán</h1>
          <p>
            Sometimes you may want to assign a post a specific tag, but you
            don't necessarily want that tag appearing in the theme or creating
            an archive page. In Ghost, hashtags are private and can be used for
            special styling.
          </p>
          <p>
            For example, if you sometimes publish posts with video content - you
            might want your theme to adapt and get rid of the sidebar for these
            posts, to give more space for an embedded video to fill the screen.
            In this case, you could use private tags to tell your theme what to
            do.
          </p>
          <blockquote>
            <em>
              <strong>News</strong>, #video
            </em>
          </blockquote>
          <p>
            Here, the theme would assign the post publicly displayed tags of
            News - but it would also keep a private record of the post being
            tagged with #video. In your theme, you could then look for private
            tags conditionally and give them special formatting.{" "}
          </p>
          <blockquote>
            <em>
              You can find documentation for theme development techniques like
              this and many more over on Ghost's extensive{" "}
              <a href="https://themes.ghost.org/v2.0.0/docs">
                theme documentation
              </a>
              .
            </em>
          </blockquote>
          <h1 id="dynamic-routing">Code mẫu</h1>
          <p>
            Dynamic routing gives you the ultimate freedom to build a custom
            publication to suit your needs. Routes are rules that map URL
            patterns to your content and templates.{" "}
          </p>
          <p>
            For example, you may not want content tagged with <code>News</code>{" "}
            to exist on:
            <code>example.com/tag/news</code>. Instead, you want it to exist on
            <code>example.com/news</code> .{" "}
          </p>
          <p>
            In this case you can use dynamic routes to create customised
            collections of content on your site. It's also possible to use
            multiple templates in your theme to render each content type
            differently.
          </p>
          <p>
            There are lots of use cases for dynamic routing with Ghost, here are
            a few common examples:{" "}
          </p>
          <ul>
            <li>Setting a custom home page with its own template</li>
            <li>
              Having separate content hubs for blog and podcast, that render
              differently, and have custom RSS feeds to support two types of
              content
            </li>
            <li>
              Creating a founders column as a unique view, by filtering content
              created by specific authors
            </li>
            <li>Including dates in permalinks for your posts</li>
            <li>
              Setting posts to have a URL relative to their primary tag like
              <code>example.com/europe/story-title/</code>
              <br />
            </li>
          </ul>
          <h1 id="dynamic-routing">Minh họa</h1>
          <p>
            Dynamic routing gives you the ultimate freedom to build a custom
            publication to suit your needs. Routes are rules that map URL
            patterns to your content and templates.{" "}
          </p>
          <p>
            For example, you may not want content tagged with <code>News</code>{" "}
            to exist on:
            <code>example.com/tag/news</code>. Instead, you want it to exist on
            <code>example.com/news</code> .{" "}
          </p>
          <p>
            In this case you can use dynamic routes to create customised
            collections of content on your site. It's also possible to use
            multiple templates in your theme to render each content type
            differently.
          </p>
          <p>
            There are lots of use cases for dynamic routing with Ghost, here are
            a few common examples:{" "}
          </p>

          <blockquote>
            <em>
              Dynamic routing can be configured in Ghost using{" "}
              <a
                href="http://yaml.org/spec/1.2/spec.html"
                rel="noreferrer nofollow noopener"
              >
                YAML
              </a>{" "}
              files. Read our dynamic routing{" "}
              <a href="https://docs.ghost.org/docs/dynamic-routing">
                documentation
              </a>{" "}
              for further details.
            </em>
          </blockquote>
          <p>
            You can further customise your site using{" "}
            <a href="../apps-integrations/index.html">
              Apps &amp; Integrations
            </a>
            .
          </p>
          <h1 id="dynamic-routing">Ứng dụng của thuật toán</h1>
          <p>
            Dynamic routing gives you the ultimate freedom to build a custom
            publication to suit your needs. Routes are rules that map URL
            patterns to your content and templates.{" "}
          </p>
          <p>
            For example, you may not want content tagged with <code>News</code>{" "}
            to exist on:
            <code>example.com/tag/news</code>. Instead, you want it to exist on
            <code>example.com/news</code> .{" "}
          </p>
          <p>
            In this case you can use dynamic routes to create customised
            collections of content on your site. It's also possible to use
            multiple templates in your theme to render each content type
            differently.
          </p>
          <p>
            There are lots of use cases for dynamic routing with Ghost, here are
            a few common examples:{" "}
          </p>
        </div>
      </section>
    );
  }
}

export default Section1;