import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import {TAGS} from '../../constants/data';

class NavMain extends Component {
    render() {
        var items = [];

        for (var key in TAGS){
            var LinkTo = "/tag/" + key;
            items.push(
                <li className="nav-code" role="menuitem" key={key}>
                    <NavLink to={LinkTo}>{TAGS[key]}</NavLink>
                </li>);
        }
        
        return (
            <ul className="nav" role="menu">
                {items}
                <li className="nav-resume" role="menuitem">
                    <a href="https://hmthanh.github.io">About me</a>
                </li>
            </ul>
        );
    }
}

export default NavMain;