/* eslint-disable jsx-a11y/accessible-emoji */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {BLOG_NAME} from '../../constants/const'; 
import PostCard from '../PostCard/PostCard';
import * as Data from '../../constants/data';

class Aside extends Component {
    render() {
        var { tagName, tag } = this.props;
        
        
        return (

            <aside className="read-next outer">
                <div className="inner">
                    <div className="read-next-feed">
                        <article className="read-next-card"
                            style={{ backgroundImage: "url(../img/page/2/cover.jpg)" }}>
                            <header className="read-next-card-header">
                                <small className="read-next-card-header-sitetitle">&mdash; {BLOG_NAME} &mdash;</small>
                                <h3 className="read-next-card-header-title">
                                <a href={"../tag/" + tag}>{tagName}
                                </a></h3>
                            </header>
                            <div className="read-next-divider"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                                <path
                                    d="M13 14.5s2 3 5 3 5.5-2.463 5.5-5.5S21 6.5 18 6.5c-5 0-7 11-12 11C2.962 17.5.5 15.037.5 12S3 6.5 6 6.5s4.5 3.5 4.5 3.5" />
                            </svg>
                            </div>
                            <div className="read-next-card-content">
                                <ul>
                                    <li><Link to="/pages/2">Welcome to Ghost</Link></li>
                                    <li><Link to="/pages/2">Writing posts with Ghost ✍️</Link></li>
                                    <li><Link to="/pages/2">Publishing options</Link></li>
                                </ul>
                            </div>
                            <footer className="read-next-card-footer">
                                <a href="../tag/getting-started/index.html">See all 6 posts →</a>
                            </footer>
                        </article>

                        <PostCard id={0} data={Data.PostInfo[0]}></PostCard>
                        <PostCard id={1} data={Data.PostInfo[1]}></PostCard>
                    </div>
                </div>
            </aside>
        );
    }
}

export default Aside;